/* Variables
/* ---------------------------------------------------------- */

:root {
    /* Colors */
    --color-light:      #F4F0F9; /* Magnolia */
    --color-dark:       #130c1d; /* Xiketic */
    --color-primary:    #8256BF; /* Royal Purple */
    --color-secondary:  #476EBD; /* Han Blue */
    --color-tertiary:   #23967f; /* Paolo Veronese Green */

    --white:            var(--color-light);
    --light-gray:       #CECCCC; /* Light Gray */
    --dark-gray:        var(--color-dark);
    --black:            #0F110C; /* Rich Black */
    --green:            var(--color-tertiary);
    --red:              #A30015; /* Ruby Red*/

    --color-text-onLight:   var(--dark-gray);
    --color-text-onDark:    #F4F0F9D0;
    --color-text-onPrimary: var(--color-light);
}
/* Default/Light Theme */
:root.theme-default,
:root.theme-light {
    --text:         var(--color-text-onLight);
    --bg:           var(--color-light);
    --border:       var(--dark-gray);
    --highlight:    var(--color-primary);
    --reverse-text: var(--color-light);
    --reverse-bg:   var(--dark-gray);
    --field-text:   var(--black);
    --field-bg:     var(--light-gray);

    transition: var(--transition);
}
/* Dark Theme */
:root.theme-dark {
    --text:         var(--color-text-onDark);
    --bg:           var(--dark-gray);
    --border:       var(--color-primary);
    --highlight:    var(--color-primary);
    --reverse-text: var(--color-text-onLight);
    --reverse-bg:   var(--color-light);
    --field-text:   var(--black);
    --field-bg:     var(--light-gray);
    transition: var(--transition);
}
/* Auto Dark Theme based on OS/browser settings */
@media (prefers-color-scheme: dark) {
    :root.theme-default {
        --text:         var(--color-text-onDark);
        --bg:           var(--dark-gray);
        --border:       var(--color-primary);
        --highlight:    var(--color-primary);
        --reverse-text: var(--color-text-onLight);
        --reverse-bg:   var(--color-light);
        --field-text:   var(--black);
        --field-bg:     var(--light-gray);
        transition: var(--transition);
    }
}
:root {
    /* Fonts */
    --font-fancy:   'Venera', sans-serif;
    --font-sans-serif:  'Muli', sans-serif;
    --font-serif:   Georgia, Times, serif;
    --font-mono:    Menlo, Courier, monospace;
    --font-light:   100;
    --font-regular: 400;
    --font-semi:    600;
    --font-bold:    700;
    --font-heavy:   800;

    --font-size-min: 18px;
    --font-size-max: 22px;

    --font-smaller: .6em;
    --font-small:   .75em;
    --font-normal:  1em;
    --font-big:     1.2em;
    --font-bigger:  1.4em;

    --font-h6: 1rem;
    --font-h5: 1.2rem;
    --font-h4: 1.4rem;
    --font-h3: 1.6rem;
    --font-h2: 2rem;
    --font-h1: 2.6rem;

    /* Breakpoints */
    --xlarge:   1680px;
    --large:    1280px;
    --medium:   980px;
    --small:    740px;
    --xsmall:   480px;

    /* Sizes */
    --height:   2em;
    --margin:   1.5rem;
    --margin-large: calc(var(--margin) * 2);
    --radius:   0.5em;
    --padding:  2vw;
    --padding-vertical: 2vh;
    --padding-large: 3.5vw;

    /* Misc */
    --transition: all 0.4s ease-out;
}









